 @content; }\n}\n\n@mixin lg {\n  @media (min-width: 1024px) { @content; }\n}\n\n@mixin xl {\n  @media (min-width: 1280px) { @content; }\n}\n\n@mixin xxl {\n  @media (min-width: 1536px) { @content; }\n}\n\n// Typography scales\n$font-size-xs: 0.75rem;   // 12px\n$font-size-sm: 0.875rem;  // 14px\n$font-size-base: 1rem;    // 16px\n$font-size-lg: 1.125rem;  // 18px\n$font-size-xl: 1.25rem;   // 20px\n$font-size-2xl: 1.5rem;   // 24px\n$font-size-3xl: 1.875rem; // 30px\n$font-size-4xl: 2.25rem;  // 36px\n$font-size-5xl: 3rem;     // 48px \n\n// Button gradients\n$button-gradient-primary: linear-gradient(to right, var(--color-lime-300, #a3e635), var(--color-emerald-400, #34d399));\n$button-gradient-primary-hover: linear-gradient(to right, var(--color-lime-400, #84cc16), var(--color-emerald-500, #10b981));\n$button-gradient-secondary: linear-gradient(to right, var(--color-gray-700, #374151), var(--color-gray-800, #1f2937));\n\n:root {\n  // Button gradients\n  --button-gradient-primary: #{$button-gradient-primary};\n  --button-gradient-primary-hover: #{$button-gradient-primary-hover};\n  --button-gradient-secondary: #{$button-gradient-secondary};\n} ","// Canonical design system import - MUST BE FIRST\n@use '../../../styles/design-system' as ds;\n\n// Card Component Styles\n// Uses design system tokens for consistent theming and responsiveness\n\n// Base card placeholder\n%card-base {\n    background-color: var(--card-bg-color, var(--color-surface-primary, #ffffff));\n    border-radius: var(--card-border-radius, var(--radius-lg, 0.5rem));\n    border: 1px solid var(--card-border-color, var(--color-border-light, #e5e7eb));\n    box-shadow: var(--card-shadow, var(--shadow-md, 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)));\n}\n\n// Button placeholder\n%button-base {\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    padding: var(--spacing-2) var(--spacing-4);\n    border-radius: var(--radius-md);\n    font-weight: var(--font-weight-semibold);\n    transition: all 0.3s ease;\n    border: none;\n    cursor: pointer;\n}\n\n// Base card styles\n.card {\n    @extend %card-base;\n    position: relative;\n    overflow: hidden;\n    transition: var(--transition-standard);\n\n    // Theme-specific styles\n    &[data-theme=\"gym\"] {\n        --card-bg-color: var(--color-surface-primary);\n        --card-border-color: var(--color-border-light);\n        --card-shadow: var(--shadow-md);\n    }\n\n    &[data-theme=\"sports\"] {\n        --card-bg-color: var(--color-surface-primary);\n        --card-border-color: var(--color-border-light);\n        --card-shadow: var(--shadow-md);\n    }\n\n    &[data-theme=\"wellness\"] {\n        --card-bg-color: var(--color-surface-primary);\n        --card-border-color: var(--color-border-light);\n        --card-shadow: var(--shadow-md);\n    }\n\n    // Responsive padding\n    padding: var(--spacing-4);\n\n    @media (min-width: var(--breakpoint-md)) {\n        padding: var(--spacing-6);\n    }\n\n    @media (min-width: var(--breakpoint-lg)) {\n        padding: var(--spacing-8);\n    }\n\n    // Card variants\n    &--content {\n        --card-content-spacing: var(--spacing-4);\n\n        h2 {\n            color: var(--color-text-primary);\n            font-size: var(--font-size-lg);\n            font-weight: var(--font-weight-semibold);\n            margin-bottom: var(--card-content-spacing);\n        }\n\n        p {\n            color: var(--color-text-secondary);\n            font-size: var(--font-size-base);\n            line-height: var(--line-height-relaxed);\n        }\n    }\n\n    &--profile {\n        --card-profile-spacing: var(--spacing-4);\n        text-align: center;\n\n        .card-avatar {\n            width: 80px;\n            height: 80px;\n            border-radius: 50%;\n            margin-bottom: var(--card-profile-spacing);\n            border: 2px solid var(--color-border-light);\n        }\n\n        h2 {\n            color: var(--color-text-primary);\n            font-size: var(--font-size-lg);\n            font-weight: var(--font-weight-semibold);\n            margin-bottom: var(--spacing-2);\n        }\n\n        p {\n            color: var(--color-text-secondary);\n            font-size: var(--font-size-base);\n            line-height: var(--line-height-relaxed);\n        }\n    }\n\n    &--workout {\n        --card-workout-spacing: var(--spacing-4);\n\n        h2 {\n            color: var(--color-text-primary);\n            font-size: var(--font-size-lg);\n            font-weight: var(--font-weight-semibold);\n            margin-bottom: var(--card-workout-spacing);\n        }\n\n        .card-difficulty {\n            display: inline-block;\n            padding: var(--spacing-1) var(--spacing-2);\n            border-radius: var(--radius-full);\n            font-size: var(--font-size-sm);\n            font-weight: var(--font-weight-medium);\n            background-color: var(--color-accent-400-alpha-10);\n            color: var(--color-accent-400);\n            margin-bottom: var(--spacing-2);\n        }\n\n        .card-duration {\n            display: block;\n            color: var(--color-text-secondary);\n            font-size: var(--font-size-sm);\n            margin-bottom: var(--spacing-4);\n        }\n\n        button {\n            @extend %button-base;\n            background: var(--button-default-primary-gradient);\n            color: var(--color-text-primary);\n            box-shadow: var(--button-default-primary-shadow);\n\n            &:hover {\n                background: var(--button-default-primary-gradient-hover);\n                box-shadow: var(--button-default-primary-shadow-hover);\n                transform: translateY(-2px);\n            }\n        }\n    }\n\n    &--program {\n        --card-program-spacing: var(--spacing-4);\n\n        h2 {\n            color: var(--color-text-primary);\n            font-size: var(--font-size-lg);\n            font-weight: var(--font-weight-semibold);\n            margin-bottom: var(--card-program-spacing);\n        }\n\n        .card-level {\n            display: inline-block;\n            padding: var(--spacing-1) var(--spacing-2);\n            border-radius: var(--radius-full);\n            font-size: var(--font-size-sm);\n            font-weight: var(--font-weight-medium);\n            background-color: var(--color-accent-400-alpha-10);\n            color: var(--color-accent-400);\n            margin-bottom: var(--spacing-2);\n        }\n\n        p {\n            color: var(--color-text-secondary);\n            font-size: var(--font-size-base);\n            line-height: var(--line-height-relaxed);\n        }\n    }\n\n    // Media container\n    .card-media {\n        margin: calc(-1 * var(--card-padding));\n        margin-bottom: var(--card-padding);\n\n        img,\n        video {\n            width: 100%;\n            height: auto;\n            object-fit: cover;\n        }\n    }\n\n    // Loading state\n    &[data-loading=\"true\"] {\n        &::before {\n            content: '';\n            position: absolute;\n            inset: 0;\n            background: linear-gradient(90deg,\n                    transparent,\n                    var(--color-overlay-light),\n                    transparent);\n            animation: shimmer 1.5s infinite;\n        }\n    }\n}\n\n// Loading animation\n@keyframes shimmer {\n    0% {\n        transform: translateX(-100%);\n    }\n\n    100% {\n        transform: translateX(100%);\n    }\n}"],"sourceRoot":""}]);
